    <h1>María Zambrano Alarcón</h1>
    <p> (Vélez-Málaga, Málaga, 22 de abril de 1904 – Madrid, 6 de febrero de 1991)1​ fue una intelectual, filósofa y ensayista española.1​ Su extensa obra, entre el compromiso cívico y el pensamiento poético, no fue reconocida en España hasta el último cuarto del siglo XX, tras un largo exilio. Ya anciana, recibió los dos máximos galardones literarios concedidos en España: el Premio Príncipe de Asturias en 1981, y el Premio Cervantes en 1988.</p>
    <p>En 1924 su familia se trasladó de nuevo a Madrid, donde se matriculó por libre (debido a su escasa salud) en la Facultad de Filosofía y Letras de la Universidad. Entre 1924 y 1926 asiste a las clases de García Morente, Julián Besteiro, Manuel Bartolomé Cossío y Xavier Zubiri en la Universidad Central de Madrid, también conoce a Ortega y Gasset en un tribunal de exámenes.nota 4​ En 1927 es invitada a la tertulia de la Revista de Occidente, círculo en el que a pesar de su juventud asumiría un papel de mediadora entre Ortega y Gasset y algunos escritores jóvenes, como Antonio Sánchez Barbudo o José Antonio Maravall.

    <p>A partir de 1928 comenzó su doctorado e ingresó en la Federación Universitaria Escolar (FUE), donde comienza a colaborar en la sección "Aire Libre" del periódico madrileño El Liberal. Participa en la fundación de la Liga de Educación Social, de la que será vocal. También imparte clases de filosofía en el Instituto Escuela que se vieron interrumpidas por una nueva recaída de su salud (en esta ocasión el diagnóstico es concreto: tuberculosis).nota 5​ No interrumpió sin embargo sus colaboraciones con la FUE y muchos de sus escritos.</p>

    <p>En 1931 fue nombrada profesora auxiliar de Zubiri en la cátedra de Historia de la Filosofía en la Universidad Central (puesto que, haciendo las sustituciones a Zubiri cuando está de viaje, ocuparía hasta el año 1935); en esa época inició su inconclusa tesis doctoral sobre «La salvación del individuo en Spinoza». Integrada en el aparato de la coalición republicano-socialista, asistió a la proclamación de la Segunda República Española en la Puerta del Sol el 14 de abril de 1931; no aceptó, sin embargo, la oferta de una candidatura a las Cortes como diputada por el PSOE.</p>
    <p>El 28 de enero de 1939 María cruzó la frontera francesa en compañía de su madre, su hermana Araceli, el marido de esta y otros familiares.nota 9​ En Francia, María se reencuentra con su marido y tras una breve estancia en París, parten para México invitados por la Casa de España, recalando antes en Nueva York y La Habana, donde fue invitada como profesora de la Universidad y del Instituto de Altos Estudios e Investigaciones Científicas. De Cuba pasó a México, donde —tras una serie de maniobras a cargo de algunos antiguos colegas— fue nombrada profesora en la Universidad Michoacana de San Nicolás de Hidalgo de Morelia, (Michoacán), lo que supuso para María un pequeño exilio dentro del gran exilio y provocó que abandonase México, viviendo unos años entre Puerto Rico y Cuba.17​ En México publicaría sus libros Filosofía y poesía y Pensamiento y poesía en la vida española, y entablaría relación con Alfonso Reyes y Daniel Cosío Villegas.</p>

    <p> Entre 1940 y 1945 trabajó con intensidad en seminarios y ciclos de conferencias o dictando lecciones y cursos en diversas instituciones cubanas y puertorriqueñas, como el Departamento de Estudios Hispánicos de la Universidad de Puerto Rico, la Asociación de Mujeres Graduadas y el Ateneo, o la Asamblea de Profesores de Universidad en el exilio reunida en La Habana. Paralelamente continúa publicando artículos y algunos libros como La Confesión: Género Literario y Método, La agonía de Europa o El pensamiento vivo de Séneca. La II guerra mundial le impide reunirse con su madre enferma y su hermana Araceli, viuda y en el umbral de la locura, que sobreviven en el París ocupado por los nazis.</p>
    <p>Liberada al fin la capital francesa, los lentos trámites de su visado harán que cuando María llegue a París su madre ya esté enterrada, y su hermana "enterrada en vida", situación que lleva a la pensadora española a tomar la decisión de no volver a separarse de Araceli. Así, en 1947, las hermanas Zambrano se instalaron en un apartamento de la Rue de L'Université, hogar eventual al que en marzo de aquel año se incorporó el marido de María. La convivencia resultó ser insostenible.</p>

    <p> En 1948, ya solas y unidas hasta el final, María y Araceli Zambrano se trasladaron a La Habana, y de allí a México y de nuevo a La Habana. Pero la situación económica empieza a ser agobiante y deciden volver a Europa. Su condición de seres errabundos en continuo destierro empieza a ser casi obsesiva. En 1949 las hermanas Zambrano volvieron a Europa instalándose en Roma hasta junio de 1950, en que el gobierno italiano se niega a prolongar sus permisos de residencia. Marchan a París donde María se reencuentra con su marido, la relación será breve y finalmente Rodríguez Aldave y su hermano (con el que ha vivido los tres últimos años, desde 1947), partieron hacia México.</p>

    <p>Las hermanas Zambrano permanecieron en París hasta marzo de 1953, fecha en que de nuevo se trasladaron a La Habana. Pero la inicial euforia del reencuentro caribeño se disolverá en la marea de la situación política cubana, las añoranzas de Araceli, la relación amorosa de María con Pittaluga y sus propios fantasmas.18​ En junio de 1953 un barco las devolvió a Roma.</p>
    <p>En 1981 fue recompensada con el Premio Príncipe de Asturias de Comunicación y Humanidades, en su primera edición. A su vez, el ayuntamiento de Vélez-Málaga, su ciudad natal, la nombró Hija Predilecta. Al año siguiente, el 19 de diciembre, la Junta de Gobierno de la Universidad de Málaga acordó su nombramiento como Doctora honoris causa.</p>

    <p>Tras una recaída en su salud y que los médicos la declarasen desahuciada, la ya anciana pero aún lúcida pensadora se recuperó y el 20 de noviembre de 1984,nota 12​ María Zambrano regresó por fin a España tras casi medio siglo de exilio. Se instaló en Madrid, ciudad de la que saldría ya en pocas ocasiones. En esta última etapa la actividad intelectual fue, sin embargo, incansable. Continuaron también los reconocimientos oficiales: Hija Predilecta de Andalucía en 1985, y en 1987, se constituye en Vélez-Málaga la fundación que lleva su nombre. Finalmente, en 1988 se le concedió el Premio Cervantes. Murió en Madrid el 6 de febrero de 1991, y fue enterrada entre un naranjo y un limonero en el cementerio de Vélez-Málaga, donde luego se trasladaron también los restos mortales de sus "dos Aracelis", su madre y su hermana. En la lápida puede leerse a modo de epitafio, el verso del Cantar de los Cantares, «Surge amica mea et veni».</p>

    <p>Siguió recibiendo reconocimientos a título póstumo, muchos de ellos reivindicaciones autonómicas: Hija Predilecta de la Provincia de Málaga en 2002. El 27 de noviembre de 2006 el Ministerio de Fomento bautizó con su nombre la estación central de ferrocarril de Málaga. En 2008 se botó el buque remolcador de salvamento marítimo, María Zambrano (BS-22). Igualmente lleva su nombre la BIblioteca Central de la que es su Alma Máter, la Universidad Complutense de Madrid. El 28 de abril de 2017, el Pleno del Ayuntamiento de Segovia aprobó por unanimidad la concesión del título de Hija Adoptiva y Predilecta de Segovia. El campus universitario de la ciudad lleva también su nombre.</p>

